/* GRID BLOCK */
/*
<main class="has-title width-max">
  <figure>
    <img src="" alt="">
    <figcaption class="wysiwyg">
      *** WISYWIG
    </figcaption>
  </figure>
</main>
*/
section.grid-block:nth-child(1):after{
      background-image: -webkit-linear-gradient(top, rgba(23, 24, 32, 0.95), rgba(23, 24, 32, 0.95)), url(banner-overlay.png);
      content: '';
      display: block;
      height: 100%;
      left: 0;
      position: absolute;
      top: 0;
      width: 100%;
      opacity: 0.93;
}

section.grid-block:nth-child(1){
  >main{
    z-index: 1;
  }
}


section.grid-block:nth-child(2) {
  >figure{
    background-attachment: fixed;
    background-position: center center;
    background-size: cover;
    box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.25);
    height: 100vh;
    overflow: hidden;
  }
  >main {
    background: rgba(23, 24, 32, 0.95);
    border-style: solid;
    opacity: 1;
    position: absolute;
    margin: 20% auto !important;
    border-color: #e44c65;
    border-top-width: 0.35em;
    article:not(:nth-child(1)) p{
      padding: 5rem .25em .625rem;
    }
  }
}
.grid-block.desktop {
    box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.25);
    min-height: 100vh;
    position: relative;
    text-align: center;
    z-index: 21;
  > main {
    margin: 15% auto !important;
    display: grid;
    row-gap: pix($block-padding / 2);
    column-gap: pix($block-padding / 2);
    grid-auto-columns: 1fr;
    grid-auto-rows: 1fr;
    width: 100% !important;
    
  }
  > header{
    padding-top: 0 !important;
    
  }
  > header h2{
    padding-top: 0 !important;
    
  }
}
.grid-block.mobile {
    box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.25);
    min-height: 100vh;
    position: relative;
    text-align: center;
    z-index: 21;
  > main {
    margin: 15% auto;
    display: grid;
    row-gap: pix($block-padding / 2);
    grid-auto-rows: 1fr;
  }
}

.grid-block {
    h2 {
      margin-top: 5rem;
      padding-bottom: 2.5rem;
    }
    hr {
    
    width: 5%;
    border-top: 2px solid $color-nax;
   }

  > main {
    width: 100% !important;
    margin: 15% auto !important;
    .rounded {
      border-radius: pix($border-radius-default);
    }
    > article {
      display: flex;
      flex-direction: column;
      overflow: hidden;
      > h3 {
        padding-top: pix($block-padding * 2);
        padding-bottom: pix($block-padding / 4);
        padding-left: 0.25em;
        padding-right: 0.25em;
      }
      > div {
        padding-top: pix($block-padding / 4);
        padding-bottom: pix($block-padding * 2);
        padding-left: 1em;
        padding-right: 1em;
        //white-space: pre-wrap;
        span.author_name{
          color: $color-nax;
        }
      }
      > figure {
        width: 100%;
        height: 100%;
        background-repeat: no-repeat;
        border-radius: 100% !important;
        display: inline-block;
        height: 18em;
        margin-left: 3em;
        vertical-align: middle;
        width: 18em;
        
      }
      > iframe {
        width: 100%;
        flex-grow: 1;
        border: none;
        margin: 0;
        padding: 0;
      }
    }
  }
}

@media (min-width: 320px) and (max-width: 1180px){
section.grid-block, .icon-bullet-list-block, section.icon-text-list-block{
  > main {
    width: 100% !important;
    padding: 1rem !important;
  }
 }
}
@media (min-width: 320px) and (max-width: 800px){
  section.bullet-list-block{
    > main {
      width: 100% !important;
      padding: 1rem !important;
    }
  }
}
@media (min-width: 650px) and (max-width: 900px){
  section.grid-block, section.icon-text-list-block{
    > main {
      grid-template-columns: repeat(2, 1fr) !important;
    }
   }
  }
  @media (min-width: 320px) and (max-width: 649px){
    section.grid-block, section.icon-text-list-block{
      > main {
        grid-template-columns: repeat(1, 1fr) !important;
      }
     }
    }

    @media (min-width: 320px) and (max-width: 650px){
      .icon-bullet-list-block{
        > main {
          grid-template-columns: auto !important;
        }
       }
      }

      @media (min-width: 320px) and (max-width: 1180px){
        section.contact-form-block{
          > main {
            width: 100% !important;
          }
         }
        }
      @media (min-width: 320px) and (max-width: 700px){
        section.contact-form-block{
          > main {
            padding: 1rem !important;
          }
         }
        }


        @media (min-width: 320px) and (max-width: 550px){
           h2 {
            padding: 0 0 !important;
            font-size: 2em;
           }
           .subtitle2{
            font-size: 2em;
           }
           #site-footer .footer-content p{
             text-align: center !important;
           }
           section.menu header.menu-item a.link{
             display: inline-block !important;
           }
           section.grid-block header{
            padding-top: 4rem !important;
           }
           section header{
             padding-top: 2rem !important;
             padding-bottom: 1rem !important;
             padding-left: 20px;
             padding-right: 20px;
           }
          }
